import Dog from "./Dog";
import React, { useEffect, useState } from "react";
import axios from "axios";
import { Box, Container } from "@mui/material";
import { SERVER_URL } from "../config/Constants";

import MOCKDATA from "./MockData";
import TypeSelector from "./TypeSelector";
import PageComponent from "./PageComponent";

const DogList = () => {
  const [dogsData, setDogsData] = useState([]);
  const [pageInfo, setPageInfo] = useState({
    type: "recent",
    page: 1,
    size: 9,
  });
  const [totalPages, setTotalPages] = useState(20);

  const [loading, setLoading] = useState(true); //   

  useEffect(() => {
    console.log("PageInfo:" + JSON.stringify(pageInfo));
    // dogListAPI(pageInfo);
    fetchMockData();
  }, [pageInfo]);

  const handleTypeChange = (e, newType) => {
    e.preventDefault();
    if (newType !== null) {
      setPageInfo((prevInfo) => ({
        ...prevInfo,
        type: newType,
        page: 1,
      }));
    }
  };

  const fetchMockData = () => {
    setLoading(true); //   
    setTimeout(() => {
      const totalDogs = MOCKDATA.length;
      setTotalPages(Math.ceil(totalDogs / pageInfo.size)); //    

      const startIndex = (pageInfo.page - 1) * pageInfo.size;
      const endIndex = startIndex + pageInfo.size;

      setDogsData(MOCKDATA.slice(startIndex, endIndex)); //   
      setLoading(false); //   
      console.log(dogsData);
    }); //   1  
  };

  const handlePageChange = (page) => {
    setPageInfo((prevInfo) => ({
      ...prevInfo,
      page,
    }));
  };

  const dogListAPI = async () => {
    const token = sessionStorage.getItem("jwt");
    try {
      const response = await axios.get(SERVER_URL + "dog/findAll", {
        headers: { Authorization: token },
        params: {
          type: pageInfo.type,
          page: pageInfo.page,
          size: pageInfo.size,
        },
      });
      setDogsData(response.data._embedded.dogs);
      setTotalPages(Math.ceil(response.data.total / pageInfo.size));
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <Container>
      {loading ? (
        <div>Loading...</div> //      
      ) : (
        <>
          <TypeSelector value={pageInfo.type} onChange={handleTypeChange} />
          <Box
            sx={{
              display: "grid",
              gridTemplateColumns: "repeat(3,1fr)",
              gap: 8,
            }}
          >
            {dogsData.map((dog) => (
              <Box key={dog.dogId}>
                <Dog dog={dog} />
              </Box>
            ))}
          </Box>
          <PageComponent
            pageInfo={pageInfo}
            totalPages={totalPages}
            onPageChange={handlePageChange}
            onPrev={() => handlePageChange(Math.max(pageInfo.page - 1, 1))}
            onNext={() =>
              handlePageChange(Math.min(pageInfo.page + 1, totalPages))
            }
          />
        </>
      )}
    </Container>
  );
};

export default DogList;
