import React from "react";
import BoardCustomerCenterMoveButton from "./BoardCustomerCenterMoveButton";
import Banner1 from "../../static/Banner1.png";
import "../../config/Utility.css";
import "./css/FreeBoardBanner.css";
import Button from "@mui/material/Button";
import { useNavigate } from "react-router-dom";

const FreeBoardBanner = () => {
  const navigate = useNavigate();
  return (
    <div className="flex flex-column">
      <Button className="oneOnOneButton absolute" variant="text" onClick={() => {navigate('/Board')}}>{/*     */}
      1:1    
      </Button>      
      <div className="relative banner-margin-0">
        <img src={Banner1} alt="  " />
        <h2 className="banner-text absolute"></h2>
        <span className="banner-description absolute">   .</span>
        <section>
          <BoardCustomerCenterMoveButton />
        </section>
      </div>
    </div>
  );
};

export default FreeBoardBanner; 
