import React from "react";
import { Box, Typography, Button, Grid2, Card, CardMedia } from "@mui/material";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import ShareIcon from "@mui/icons-material/Share";
import Categories from "./Categories";

import MainImage from "./MainImage";

const DogInfo = () => {
  const infoItems = [
    { label: "", value: "223%", extra: " 50,002,000" },
    { label: "", value: "10", extra: "2024-10-02 " },
    { label: "", value: "41" },
  ];

  const categories = ["", "", ""]; // '', '', '' 
  const title = "  |  |    ";
  const imgSrc =
    "//image-se.ycrowdy.com/20240909/CROWDY_202409091318250233_oSBCe.jpg/ycrowdy/resize/!740x!417";

  const WarningMessage = () => {
    return (
      <Typography
        variant="caption"
        sx={{
          marginTop: 2,
          display: "block",
          color: "gray",
          borderRadius: "6px",
          backgroundColor: "#fef1f1",
          fontSize: "12px",
          padding: "5px 12px",
          lineHeight: "18px",
          textAlign: "center",
        }}
      >
          <b> </b>    <b> </b>
          .      
        .
      </Typography>
    );
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: 2,
        gap: 4,
      }}
    >
      {/*   */}
      <Box sx={{ flex: 2, marginRight: 2 }}>
        <Typography
          variant="h6"
          fontSize={"32px"}
          fontWeight={"bold"}
          letterSpacing={"-1px"}
          lineHeight={"50px"}
        >
          {title}
        </Typography>

        {/*   */}
        <MainImage imgSrc={imgSrc} />
      </Box>

      {/*   */}
      <Box sx={{ flex: 1, alignContent: "center" }}>
        {/*   */}
        <Categories categories={categories} />

        {/*   */}
        <div className="flex">
          <Typography variant="h4" component="span" sx={{ fontWeight: "bold" }}>
            111,527,000
          </Typography>
          <Typography variant="body1" component="span">
            {" "}
            
          </Typography>
        </div>

        {/* , ,   */}
        <Grid2 container spacing={2} sx={{ marginTop: 2 }}>
          {infoItems.map((item, index) => (
            <Grid2 key={index} xs={12} sm={6}>
              <Typography variant="body2">
                {item.label}: <strong>{item.value}</strong>{" "}
                {item.extra && `| ${item.extra}`}
              </Typography>
            </Grid2>
          ))}

          {/*  */}
          <Grid2 xs={12}>
            <Button
              variant="outlined"
              sx={{ marginRight: 1 }}
              startIcon={<FavoriteBorderIcon />}
            >
              17 
            </Button>
            <Button variant="outlined" startIcon={<ShareIcon />}>
              
            </Button>
          </Grid2>
        </Grid2>

        {/*   */}
        <WarningMessage />
      </Box>
    </Box>
  );
};

export default DogInfo;
