import React from "react";
import styled from "styled-components";

const Container = styled.div`
  margin-top: 20px;
`;

const BoxWrapper = styled.div`
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 10px 0;
`;

const Title = styled.h2`
  text-align: left;
  margin: 0;
  padding: 0;
`;

const HistorySection = styled.div`
  margin: 20px 0;
  width: 100%;
  height: 400px;
  gap: 5%;
`;

const PicturesSection = styled.div`
  row-gap: 0px;
  column-gap: 30px;
  padding: 20px;

  img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    box-shadow: 0 0 10px 5px #666;
  }
  img:nth-child(even) {
    transform: translateY(70px); /*      */
  }
`;

const DogInfoSection = styled.div`
  flex: 1;
  padding: 0, 20px;
  width: 100%;
  height: 100%;
`;

const historyDescription = `          . 
   <strong>  </strong>  <strong> </strong>  . 
   <strong>  </strong> <strong> </strong>  . 
  <strong> </strong>    ,     . 
  <strong>        </strong> .
`;

const images = [
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg", //    
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
];

const history = () => {
  return (
    <Container className="flex flex-column">
      <BoxWrapper className="flex w-100">
        <Title className="w-100"> History</Title>
      </BoxWrapper>
      <HistorySection className="flex flex-row">
        <PicturesSection className="pictures grid grid-cols-2 w-half">
          {images.map((src, index) => (
            <img key={index} src={src} alt={`image-${index}`} />
          ))}
        </PicturesSection>

        <DogInfoSection className="flex flex-column align-start text-left justify-center">
          <h3></h3>
          <p>: 1</p>
          <p dangerouslySetInnerHTML={{ __html: historyDescription }}></p>
        </DogInfoSection>
      </HistorySection>
    </Container>
  );
};
export default history;
