import React, { useState } from "react";
import styled from "styled-components";
import { IconButton } from "@mui/material";
import OffHeart from "@mui/icons-material/FavoriteBorderSharp";
import OnHeart from "@mui/icons-material/FavoriteSharp";
import theme from "../config/theme";

const StyledIconButton = styled.button`
  position: absolute;
  top: 8px;
  right: 8px;
  color: ${(props) =>
    props.heart === 1 ? theme.colors.red : theme.colors.white};
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  outline: none;
  box-shadow: none;
  z-index: 10;

  &:hover {
    background-color: transparent;
  }

  &:focus {
    outline: none;
  }
`;

const HeartButton = (props) => {
  const [heart, setHeart] = useState(props.initialHeart);

  const handleHeartToggle = () => {
    setHeart((prevHeart) => (prevHeart === 1 ? 0 : 1));
  };

  return (
    <StyledIconButton
      heart={heart} // styled-components  heart  
      onClick={(e) => {
        e.stopPropagation(); //     
        handleHeartToggle(); //   
      }}
    >
      {heart === 1 ? <OnHeart /> : <OffHeart />}
    </StyledIconButton>
  );
};

export default HeartButton;
