import * as React from 'react';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import RestaurantIcon from '@mui/icons-material/Restaurant'; //  
import ToysIcon from '@mui/icons-material/Toys'; //  
import ChairIcon from '@mui/icons-material/Chair'; //  
import Stroller from '@mui/icons-material/Stroller'; //  
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'; //  
import GavelIcon from '@mui/icons-material/Gavel'; //   
import {useNavigate} from "react-router-dom";

export default function Sidebar() {
    const navigate = useNavigate();
    //     
    const menuItemsGroup1 = [
        {text: '', icon: <RestaurantIcon sx={{color: 'black'}}/>},
        {text: '', icon: <ToysIcon sx={{color: 'black'}}/>},
        {text: '', icon: <ChairIcon sx={{color: 'black'}}/>},
        {text: '', icon: <Stroller sx={{color: 'black'}}/>},
    ];
    const menuItemsGroup2 = [
        {text: '', icon: <ShoppingCartIcon sx={{color: 'black'}}/>, link: '/cart'}
    ];
    const menuItemsGroup3 = [
        {text: ' ', icon: <GavelIcon sx={{color: 'black'}}/>},
    ];

    return (
        <Box sx={{display: 'flex', zIndex: 1000}}>
            <Toolbar/>
            {/*      */}
            <Box
             sx={{
             position: 'fixed', //    
             top: '70px', //    top 
             left: '50px', //    left 
             borderRadius: '10px',
             boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
             border: '1px solid #e0e0e0',
             padding: '10px',
             backgroundColor: 'white',
             width: '250px',
             height: 'calc(100vh - 130px)', //    
             overflowY: 'auto', //    
             // marginLeft: '100px', //   margin  
             // marginTop: '100px',  //   margin  
             }}

            >
                {/*     */}
                <Accordion
                    disableGutters
                    elevation={0}
                    square
                    sx={{
                        backgroundColor: 'white',
                        '&:before': {display: 'none'},
                        color: 'black',
                    }}
                >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon sx={{color: 'black'}}/>}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <ListItemText primary="" sx={{color: 'black'}}/>
                    </AccordionSummary>
                    <AccordionDetails sx={{padding: 2}}>
                        <List sx={{padding: 0}}>
                            {menuItemsGroup1.map((item) => (
                                <ListItem key={item.text} disablePadding>
                                    <ListItemButton sx={{padding: '8px 16px'}}>
                                        {/*     */}
                                        <ListItemIcon>
                                            {item.icon}
                                        </ListItemIcon>
                                        <ListItemText primary={item.text} sx={{color: 'black'}}/>
                                    </ListItemButton>
                                </ListItem>
                            ))}
                        </List>
                    </AccordionDetails>
                </Accordion>

                <Divider/>

                {/*    */}
                <List sx={{paddingBottom: 0}}>
                    {menuItemsGroup2.map((item) => (
                        <ListItem key={item.text} disablePadding>
                            <ListItemButton sx={{padding: '8px 16px'}} onClick={() => {
                                navigate(item.link)
                            }}>
                                <ListItemIcon>
                                    {item.icon}
                                </ListItemIcon>
                                <ListItemText primary={item.text} sx={{color: 'black'}}/>
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>

                <Divider/>

                {/*    */}
                <List sx={{paddingBottom: 0}}>
                    {menuItemsGroup3.map((item) => (
                        <ListItem key={item.text} disablePadding>
                            <ListItemButton sx={{padding: '8px 16px'}}>
                                <ListItemIcon>
                                    {item.icon}
                                </ListItemIcon>
                                <ListItemText primary={item.text} sx={{color: 'black'}}/>
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </Box>
        </Box>
    );
}
