import React, { useState, useEffect } from "react";
import "./css/BoardList.css";

const BoardList = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const response = await fetch("https://board/findAll");
        const data = await response.json();
        setPosts(data);
        setLoading(false);
      } catch (error) {
        console.error("  :", error);
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  if (loading) {
    return <div> ...</div>;
  }

  return (
    <div className="board-list">
      <table className="board-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {posts.length > 0 ? (
            posts.map((post) => (
              <tr key={post.id}>
                <td>{post.id}</td>
                <td>{post.title}</td>
                <td>{post.author}</td>
                <td>{post.date}</td>
                <td>{post.views}</td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="5"> .</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
};

export default BoardList;
