import { Container } from "@mui/material";
import React from "react";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { SERVER_URL } from "../config/Constants";
import axios from "axios";
import Info from "../detailComponent/Info";
import YouTubeContainer from "../detailComponent/YoutubeContainer";
import DogHistory from "../detailComponent/DogHistory";

const DogDetail = () => {
  const { dogId } = useParams();
  const [dogDetail, setDogDetail] = useState(null);
  const [price, setPrice] = useState("");
  const [selectValue, setSelectValue] = useState("");
  const options = [50000, 100000, 150000, ""]; //  

  useEffect(() => {
    const fetchDogDetail = async () => {
      try {
        const response = await axios.get(`${SERVER_URL}/findOne/${dogId}`);
        setDogDetail();
      } catch (err) {
        console.error(err);
      }
    };
  }, [dogId]);

  const handleSelectChange = (event) => {
    const selectedValue = event.target.value;
    setSelectValue(selectedValue);
    if (selectedValue === "") {
      setPrice("");
    } else {
      setPrice(selectedValue);
    }
  };

  const handleInputChange = (event) => {
    setPrice(event.target.value);
  };

  return (
    <Container className="container flex flex-column">
      <Info />

      {/* <Grid2 container spacing={2} direction="column">
        {" "}
        <Grid2 xs={6}>
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">
               .
            </InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectValue}
              label=" ."
              onChange={handleSelectChange}
            >
              {options.map((option) => (
                <MenuItem value={option} key={option}>
                  {option}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid2>
        {selectValue === "" && (
        <Grid2 xs={6}>
            <Input
              type="text"
              value={price}
              onChange={handleInputChange}
              placeholder=" "
              autoFocus
              fullWidth
            />
          </Grid2>
        )}
        <Grid2 xs={12}>
          <Box sx={{ mt: 2, p: 2, border: "1px solid black" }}>
            <Typography> : {price}</Typography>
          </Box>
        </Grid2>
      </Grid2> */}
      <YouTubeContainer />
      <DogHistory />
    </Container>
  );
};
export default DogDetail;
